import Vue from 'vue'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
// Ready translated locale messages
const messages = {
    sg: require('./lang/sg'),
    en: require('./lang/en'),
}
export default new VueI18n({
  locale: 'en', // set locale
  messages, // set locale messages
})


